import { fromJS } from 'immutable';
import { constants } from '.';

const defaultState = fromJS({
	kingWork: true,
	angelWork: true,
	nowWork: "",
	showTask: false,
	king:{"name":"","wish":""},
	photo:"",
	angelWish:"",
	comeChat: false,
});

export default (state = defaultState, action) => {
	switch(action.type) {
		case constants.ADD_MY_TASK:
			return state.merge({
				angelWork: action.angelWork,
				kingWork: action.kingWork
			});
		case constants.GET_INFO:
			return state.merge({
				king: action.result.king,
				photo: action.result.photo,
				angelWish: action.angelWish
			});
		case constants.GET_TASK_AREA:
			return state.merge({
				nowWork: action.role,
				showTask: action.showTask
			});
		case constants.CLOSE_TASK_AREA:
			return state.merge({
				showTask: action.showTask
			});
		case constants.START_CHAT:
			return state.merge({
				comeChat: true
			});
		case constants.QUIT_CHAT:
			return state.merge({
				comeChat: false
			});
		case constants.TO_DO_WORK:
			if (action.role==='king'){
				return state.merge({
					kingWork: false
				})
			}
			return state.merge({
				angelWork: false
			})
		default:
			return state;
	}
}