import styled from 'styled-components';


export const KingWrapper = styled.div`
    height: 500px;
    overflow: hidden;
    width:  100%;
    margin: 0 auto;
    background: #AAAAAA;
`;

export const AngelWrapper = styled.div`
    height: 500px;
    overflow: hidden;
    width:  100%;
    margin: 0 auto;
    background: #AAAAAA;
`;

export const SlideArea = styled.div`
    height: 100%;
    overflow: hidden;
    width:  100%;
`;

export const Avatar = styled.div`
    width: 100px;
    height: 100px;
    background-repeat:no-repeat; 
    background-size:100% 100%;
    -moz-background-size:100% 100%;
    border-radius: 50%;
    -moz-border-radius: 50%;      
    -webkit-border-radius: 50%; 
    margin: 30px auto 10px;
`;

export const Info = styled.div`
    width: 230px;
    height: 180px;
    background: #FFDDAA;
    margin: 0 auto;
    .role {
        font-family: Microsoft YaHei;
        font-size: 17px;
    }
    .wish{
        margin : 50px 0;
    }
`;

export const KingName = styled.div`
    float: right;
    width: 145px;
    height: 60px;
    text-align: center;
    line-height: 60px;
    font-size: 40px;
    margin: 0 auto;
`;

export const Wish = styled.div`
    font-size: 20px;
    float: left;
    width: 230px;
    height: 90px;
    margin: 0 auto;
`;

export const TodayTask = styled.div`
    width: 88px;
    height: 36px;
    margin: 20px auto 0; 
    text-align: center;
    line-height: 36px;
    background: #0483D9;
    border-radius: 5em;
    font-weight: 360;
    border: 2px solid #0483D9;
    transition: all 0.2s;
    position: relative;
    font-family: "HelveticaNeue-Bold", "Helvetica Neue Bold", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;
    text-decoration: none;
    color: white;
    border: 2px solid #0483D9;
    &:hover {
        background: #0483D9;
    }
`;

export const ToChat = styled.div`
    width: 88px;
    height: 36px;
    margin: 20px auto;
    text-align: center;
    line-height: 36px;
    background: #0483D9;
    border-radius: 5em;
    font-weight: 360;
    border: 2px solid #0483D9;
    transition: all 0.2s;
    position: relative;
    font-family: "HelveticaNeue-Bold", "Helvetica Neue Bold", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;
    text-decoration: none;
    color: white;
    border: 2px solid #0483D9;
    &:hover {
        background: #0483D9;
    }
`;

export const TaskArea = styled.div`
    overflow: hidden;
    z-index: 1;
    position: absolute;
    width: 100%;
    height: 530px;
`;

export const TaskBox = styled.div`
    overflow: hidden;
    position: absolute;
    width: 76%;
    height: 46%;
    margin: 60px 40px;
    background: #FFFF33;
    .day-work {
        width: auto;
        margin: 0 auto;
        font-size: 30px;
        text-align: center;
        border: 0;
        left: 0;
    };
    .top-info {
        text-align: center;
    };
    .down-info {
        width: auto
        overflow: hidden;
        margin: 20px auto 0; 
        white-space:nowrap; 
        word-break:keep-all;
    }
`;

export const AskApple = styled.div`
    width: 100px;
    height: 35px;
    line-height: 35px;
    text-align: center;
    boder-width: 0px;
    border-radius: 3px;
    background: #1F90FF;
    color: white;
    margin: 80px auto 0;
    bottom: 0;
`;

export const CloseTask = styled.div`
    position: absolute;
    top: 0;
    right: 0;
    width: 20px;
    height: 20px;
    background: #FF3366;
`;

export const ChatWrapper = styled.div`
    top: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    .chat-bottom {
      position: fixed;
      bottom: 0;
    }
`;

export const ChatTop = styled.div`
    top: 0;
    left: 0;
    height: 40px;
    width: 100%;
    font-size: 0;
    background-color:  whitesmoke;
    z-index: 1;
    .back{
        height: 40px;
        width: auto;
    }
`;

export const ChatRoom = styled.div`
    position: fixed;
    right: 0;
    height: 100%;
    width: 100%;
    border: 0;
    marigin: 0;
    background: #A8A8A8;
`;

export const Input = styled.input`
    position: fixed;
    bottom: 5px;
    height: 36px;
    width: 70%;
    padding-left: 10px;
    outline: none;
    line-height: 35px;
    font-size: 17px;
    background: #C8C8C8;
    border-radius: 50px;
    border: 1px solid #cac8ee;
    align-left: 55px;
    maxlenght: 10;
    overflow: hidden;
    color: #666666;
    border: 0;
    margin: 0;
`;

export const SendMsg = styled.button`
    position: fixed;
    height: 40px;
    width: 20%;
    background: #006699;
    margin: 0;
    border: 2px solid #006699;
    border-radius: 50px;
    bottom: 5px;
    right: 10px;
`;
