import React, { PureComponent } from 'react';
import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import {actionCreators} from './store';
import Flippy, { FrontSide, BackSide } from 'react-flippy';
import Alert from '../../components/alert';
import io from 'socket.io-client';
import Chat from './components/Chat';
import { USER_CONNECTED, COMMUNITY_CHAT, MESSAGE_SENT, MESSAGE_RECIEVED, TYPING } from '../../server/Events'
import {
  KingWrapper, 
  TaskArea, 
  AngelWrapper, 
  ToChat, 
  Avatar, 
  Info, 
  TodayTask,
  AskApple,
  TaskBox,
  CloseTask,
  KingName,
  Wish
} from './style';


const socketUrl = "http://47.93.63.56:3231"
class Home extends PureComponent {

  constructor(props) {
	  super(props);
	
	  this.state = {
      socket:null,
      user:{},
      chats:[],
	  	activeChat:null
	  };
	};

  componentWillMount() {
    this.initSocket();
	};
  
  initSocket = ()=>{
		const socket = io(socketUrl)

		socket.on('connect', ()=>{
			console.log("Connected");
		});
		
		this.setState({socket})
  };
  
  setUser = ({user})=>{
			this.setState({user})
  };

  resetChat = (chat)=>{
		return this.addChat(chat, true)
  }
  
  addChat = (chat, reset)=>{
		const { chats, socket } = this.state

		const newChats = reset ? [chat] : [...chats, chat]
		this.setState({chats:newChats, activeChat:reset ? chat : this.state.activeChat})

		const messageEvent = `${MESSAGE_RECIEVED}-${chat.id}`
		const typingEvent = `${TYPING}-${chat.id}`

		socket.on(typingEvent, this.updateTypingInChat(chat.id))
		socket.on(messageEvent, this.addMessageToChat(chat.id))
  }
  
  addMessageToChat = (chatId)=>{
		return message => {
			const { chats } = this.state
			let newChats = chats.map((chat)=>{
				if(chat.id === chatId)
					chat.messages.push(message)
				return chat
			})

			this.setState({chats:newChats})
		}
  }
  
  updateTypingInChat = (chatId) =>{
		return ({isTyping, user})=>{
			if(user !== this.state.user.name){

				const { chats } = this.state

				let newChats = chats.map((chat)=>{
					if(chat.id === chatId){
						if(isTyping && !chat.typingUsers.includes(user)){
							chat.typingUsers.push(user)
						}else if(!isTyping && chat.typingUsers.includes(user)){
							chat.typingUsers = chat.typingUsers.filter(u => u !== user)
						}
					}
					return chat
				})
				this.setState({chats:newChats})
			}
		}
  }
  
  sendMessage = (chatId, message)=>{
		const { socket } = this.state
		socket.emit(MESSAGE_SENT, {chatId, message} )
  }
  
  sendTyping = (chatId, isTyping)=>{
		const { socket } = this.state
		socket.emit(TYPING, {chatId, isTyping})
	}

	setActiveChat = (activeChat)=>{
		this.setState({activeChat})
	}
  
  getTaskArea(show, nowWork) {
    if (show) {
      if (nowWork==="king"){
        return (
          <TaskArea style={{display: show ? "":"none"}}>
            <TaskBox>
              <div className='day-work'>
                每日任务
              </div>
              <div className='top-info'>
                和你的国王愉快地聊聊吧!
              </div>
              <CloseTask  onClick={() => this.props.closeTask()}/>
              <AskApple onClick={() => this.props.toDoWork(nowWork, this.props.kingWork)} style={this.props.kingWork ? {}:{background:"#C0C0C0"}}>我要苹果</AskApple>
              <div className='down-info'>
              （收集苹果，会在us里发生神奇的事哦）
              </div>
            </TaskBox>
          </TaskArea>
        )
      } else if (nowWork==="angel"){
        return (
          <TaskArea style={{display: show ? "":"none"}}>
            <TaskBox>
              <div className='day-work'>
                Ta的任务
              </div>
              <div className='top-info'>
                和你的天使愉快地聊聊吧!
              </div>
              <CloseTask  onClick={() => this.props.closeTask()}/>
              <AskApple  onClick={() => this.props.toDoWork(nowWork, this.props.angelWork)} style={this.props.angelWork ? {}:{background:"#C0C0C0"}}>给他苹果</AskApple>
              <div className='down-info'>
              （收集苹果，会在us里发生神奇的事哦）
              </div>
            </TaskBox>
          </TaskArea>
        )
      }
    }
  };

	render() {
    const {token, comeChat, kingUuid,  startChat, showTask, getTaskArea, king, photo, angelWish, nowWork} = this.props
    if (token===''){
      return <Redirect to='/login' />
    } else {
      if (!comeChat) {
        return (
          <div>
            {this.getTaskArea(showTask, nowWork)}
            <Flippy
              flipOnHover={false} // default false
              flipOnClick={!showTask}// default false
              flipDirection="horizontal" // horizontal or vertical
              ref={(r) => this.flippy = r} // to use toggle method like this.flippy.toggle()
              // if you pass isFlipped prop component will be controlled component.
              // and other props, which will go to div
            >
              <FrontSide>
                <KingWrapper>
                    <Avatar style={{backgroundImage:'url('+ photo +')'}}/>
                    <Info>
                      <div className='role'>
                        我的国王：
                        <KingName>{king.name}</KingName>
                      </div>
                      <div className='wish'>
                        ta的心愿:
                        <Wish>{king.wish?king.wish:"ta还未填写"}</Wish>                    </div>
                    </Info>
                  <TodayTask onClick={(e) => getTaskArea(e,'king')}>每日任务</TodayTask>
                  <ToChat style={ kingUuid ? {}:{background:"#C0C0C0"}} onClick={(e) => startChat(e)}>聊一聊</ToChat>
                </KingWrapper>
              </FrontSide>
              <BackSide>
                <AngelWrapper>
                    <Avatar style={{backgroundImage:'url('+ photo +')'}}/>
                    <Info>
                      <div className='role'>
                        我的小天使：
                      </div>
                      <div className='wish'>
                        ta的心愿:
                        <Wish>{angelWish?angelWish:"ta还未填写"}</Wish>                    </div>
                    </Info>
                    <TodayTask onClick={(e) => getTaskArea(e,'angel')}>Ta的任务</TodayTask>
                  <ToChat onClick={(e) => startChat(e)}>聊一聊</ToChat>
                </AngelWrapper>
              </BackSide>
            </Flippy>
          </div>
        )
      }else{
        return <Chat socket={this.state.socket} user={this.state.user}/>
      }
    }
  };
  componentDidMount(){
    this.props.getInfoWish();
    this.props.getMyTask();
    this.state.socket.emit(USER_CONNECTED, this.props.myUuid, this.setUser);
    this.state.socket.emit(COMMUNITY_CHAT, this.resetChat)
  };
};

const mapState = (state) => ({
  token: state.getIn(['login', 'token']),
  myUuid: state.getIn(['login', 'myUuid']),
  kingUuid: state.getIn(['login', 'kingUuid']),
  angelUuid: state.getIn(['login', 'angelUuid']),
  showTask: state.getIn(['home','showTask']),
  king: state.getIn(['home','king']),
	photo: state.getIn(['home','photo']),
  angelWish: state.getIn(['home','angelWish']),
  kingWork: state.getIn(['home','kingWork']),
  angelWork: state.getIn(['home','angelWork']),
  nowWork: state.getIn(['home','nowWork']),
  comeChat: state.getIn(['home','comeChat']),
});

const mapDispatch = (dispatch) => ({
  getMyTask(){
    dispatch(actionCreators.getMyTask())
  },
  getTaskArea(e,role){
    e.stopPropagation();
    dispatch(actionCreators.getTaskArea(role))
  },
  getInfoWish(){
    dispatch(actionCreators.getInfoWish())
  },
  closeTask(){
    dispatch(actionCreators.closeTask())
  },
  toDoWork(role,work){
    if (work){
      dispatch(actionCreators.toWork(role))
    } else {
      if (role==='king'){
        Alert.open({
          alertTip:"一天只能请求一次哦",
      });
      }else if (role==='angel'){
        Alert.open({
          alertTip:"你的小天使在飞来的路上",
      });
      }
    }
  },
  startChat(e){
    e.stopPropagation();
    dispatch(actionCreators.startChat())
  },
}); 

export default  connect(mapState, mapDispatch)(Home);