import React, { PureComponent } from 'react';
import { Redirect } from 'react-router-dom';
import {WriteWish, WishButton} from '../style';
import { actionCreators } from '../store';
import { connect } from 'react-redux';

class Wish extends PureComponent {
	render() {
    const { isWish } = this.props;
    if (!isWish){
      return (
        <div>
          <WriteWish placeholder="写心愿" type="text" ref={(input) => {this.wish = input}}></WriteWish>
          <WishButton onClick={() => this.props.myWish(this.wish)}
          >NEXT</WishButton>
        </div>     
    )
      } else {
        return <Redirect to='/' />
      }
  }

}

const mapState = (state) => ({
  isWish: state.getIn(['login', 'isWish'])
});

const mapDispatch = (dispatch) => ({
  myWish(wish) {
    dispatch(actionCreators.myWish(wish.value));
  },
  getMyWish(){
    dispatch(actionCreators.getMyWish());
  }
});
  

export default connect(mapState, mapDispatch)(Wish);