import styled from 'styled-components';

export const TopImg = styled.div`
    width: 220px;
    height: 150px;
    margin: 50px auto 0;
    background: #ffffdd;
    border:1px solid #e3e197;
`;


export const LoginBox = styled.div`
    height: 250px;
    width:  100%;
    margin: 0 auto;
	padding-top: 20px
`;


export const Input = styled.input`
    display: block;
    padding: 7px 0px;
    border-radius: 3px;
    padding-left: 5px;
    height: 35px;
    width: 80%;
    margin: 20px auto 40px;
    text-align: center;
    font-size: 20px;
    font-family: Microsoft YaHei;
    :focus {
        border-style:solid;
        border-color: #03a9f4;
	    box-shadow: 0 0 15px #03a9f4;
    }
`;

export const LoginButton = styled.button`
    display: block;
    width: 180px;
    height: 45px;
    boder-width: 0px;
    border-radius: 3px;
    background: #1F90FF;
    outline: none;
    font-family: Microsoft YaHei;
    color: white;
    font-size: 17px;
    margin: 10px auto;
    text-align: center;
`;

export const WriteWish = styled.textarea`
    display: block;
    height: 200px;
    width: 78%;
    margin: 30px auto;
    border-color: red;
  	background-color:transparent;
    color: black;
    font-size: 20px;
    font-family: Microsoft YaHei;
    :focus {
        border-style:solid;
        border-color: #03a9f4;
	    box-shadow: 0 0 15px #03a9f4;
    }
`;


export const WishButton = styled.button`
    display: block;
    width: 180px;
    height: 45px;
    margin: 10px auto;
    background: linear-gradient(to bottom, #4eb5e5 0%,#389ed5 100%); /* W3C */
    border: none;
    border-radius: 5px;
    border-bottom: 4px solid #2b8bc6;
    color: #fbfbfb;
    font-weight: 600;
    font-family: 'Open Sans', sans-serif;
    text-shadow: 1px 1px 1px rgba(0,0,0,.4);
    font-size: 15px;
    text-align: left;
    text-indent: 5px;
    box-shadow: 0px 3px 0px 0px rgba(0,0,0,.2);
    cursor: pointer;
    text-align: center;
  
    
`;